.topbar {
  @extend %flex__item--full-width;
  background: tint($color-white, 50%);
  border-bottom: 1px solid tint($color-primary, 48%);
  padding: 8px 20px;
}

.topbar__logo {
  vertical-align: middle;
}

.topbar__subtitle {
  @include font-size(14px);
  display: inline-block;
  margin-left: 20px;
}
